<template lang="html">
    <x-header>
      创建队伍
    </x-header>
    <div class="team_logo">
      <div class="logo">
        <img src="../../assets/team_default_logo.png" alt="" />
        <input type="file" accept="image/jpg,image/jpeg,image/png,image/gif" multiple />
      </div>
    </div>
    <div class="weui_cells weui_cells_form">
      <div class="weui_cell">
        <div class="weui_cell_hd" style="width:5%;">
          <label class="weui_label">
            <i class="fa fa-at"></i>
          </label>
        </div>
        <div class="weui_cell_bd weui_cell_primary">
          <input class="weui_input" type="text" placeholder="队伍名称" v-model="name" />
        </div>
      </div>
      <div class="weui_cell">
        <div class="weui_cell_hd" style="width:5%;">
          <label class="weui_label">
            <i class="fa fa-tag"></i>
          </label>
        </div>
        <div class="weui_cell_bd weui_cell_primary">
          <input class="weui_input" type="text" placeholder="队伍简介" v-model="sign" />
        </div>
      </div>
    </div>
    <div class="button_sp_area" style="margin-top:30px;padding: 10px;">
      <a @click="create" href="javascript:;" class="weui_btn weui_btn_plain_primary">创 建</a>
    </div>
    <toast :show.sync="loginFail" type="text">登录失败</toast>
</template>

<script>
  import XHeader from 'vux/dist/components/x-header'
  import Toast from 'vux/dist/components/toast'

  export default {
    data: function () {
      return {
        createFail: false,
        name: '',
        sign: ''
      }
    },
    route: {
      data (transition) {
      }
    },
    methods: {
      create () {
        if (this.name === '') {
          this.$set('createFail', true)
          return
        }
        let team = {
          name: this.name,
          sign: this.sign
        }
        let accessToken = localStorage.getItem('ruzun_user_token')
        if (accessToken === null) {
          this.$route.router.go({name: 'signin'})
          return
        }
        this.$http.headers.common['HTTP_AUTHORIZATION'] = accessToken
        this.$http.post(this.$config.API + '/teams.json', {team: team}).then(function (response) {
          // 创建成功
          this.$route.router.go({name: 'team', params: {tid: response.data.team.id}})
        }, function (response) {
          // 创建失败
          this.$set('loginFail', true)
        })
      }
    },
    components: {
      XHeader,
      Toast
    }
  }
</script>

<style lang="scss">
</style>



